import React, { useState, useEffect } from 'react';
import {  Button,Row,Col,ListGroup,Image,Card, ListGroupItem } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import Message from '../components/Message';
import CheckoutSteps from '../components/CheckoutSteps';
import { createOrder } from '../actions/orderActions';
import { orderCreateReset } from '../store/order-slice';
function PlaceOrderScreen() {
    
    const dispatch = useDispatch();
    const cart = useSelector(state => state.cart);
    const itemsPrice=cart.cartItems.reduce((acc, item) =>acc+item.price*item.qty, 0).toFixed(2);
    const shippingPrice=(itemsPrice>100?0:10).toFixed(2);
    const taxPrice=Number((itemsPrice*0.1)).toFixed(2);
    const totalPrice=(Number(itemsPrice)+Number(shippingPrice)+Number(taxPrice)).toFixed(2)
    const navigate = useNavigate();
    const orderCreate=useSelector(state=>state.orderCreate)
    const { order, error, success } = orderCreate;

    if(!cart.paymentMethod){
        navigate('/payment')
    }

    useEffect(()=>{
       if(success){
        navigate(`/order/${order._id}`)
        dispatch(orderCreateReset())//after order success we need to make it reset
       }
    },[success,navigate])


    const placeOrder=()=>{
        dispatch(createOrder({
            orderItems:cart.cartItems,
            shippingAddress:cart.shippingAddress,
            paymentMethod:cart.paymentMethod,
            itemsPrice,
            shippingPrice,
            taxPrice,
            totalPrice,
        }))
    }
    
  return (
    <div>
      <CheckoutSteps step1 step2 step3 step4/>
      <Row>
        <Col md={8}>
         <ListGroup variant='flush'>
          <ListGroup.Item>
            <h2>Shipping</h2>
            <p>
                <strong>Shipping:</strong>
                {cart.shippingAddress.address}, {cart.shippingAddress.city},

                 {cart.shippingAddress.postalCode}, {cart.shippingAddress.country}
            </p>
          </ListGroup.Item>

          <ListGroup.Item>
            <h2>Payment Method</h2>
            <p>
                <strong>Method:</strong>
                {cart.paymentMethod}
                
            </p>
          </ListGroup.Item>

          <ListGroup.Item>
            <h2>Order Items</h2>
             {cart.cartItems.length ===0 ?<Message variant='info'>
                Your cart is empty
             </Message>:
             (
                <ListGroup variant='flush'>
                    {cart.cartItems.map((item,index)=>(
                        <ListGroup.Item>
                            <Row key={index}>
                            <Col md={2}>
                                <Image src={item.image} alt={item.name} fluid rounded />
                            </Col>
                            <Col>
                                <Link to={`/product/${item.product}`}>{item.name}</Link>
                            </Col>
                            <Col md={4}>
                                {item.qty} x ${item.price}=${(item.qty * item.price).toFixed(2)}
                            </Col>
                            
                            </Row>
                        </ListGroup.Item>
                    ))}
                </ListGroup>
             )
}
          </ListGroup.Item>
         </ListGroup>
        </Col>

        <Col md={4}>
        <Card>
           <ListGroup variant='flush'>
            <ListGroup.Item>
                <h2>Order Summary</h2>
            </ListGroup.Item>

            <ListGroup.Item>
                <Row>
                    <Col>
                     Items:
                    </Col>
                    <Col>
                    ${itemsPrice}
                    </Col>
                </Row>
            </ListGroup.Item>

            <ListGroup.Item>
                <Row>
                    <Col>
                     Shipping:
                    </Col>
                    <Col>
                    ${shippingPrice}
                    </Col>

                </Row>
            </ListGroup.Item>


            <ListGroup.Item>
                <Row>
                    <Col>
                     Tax:
                    </Col>
                    <Col>
                    ${taxPrice}
                    </Col>
                    
                </Row>
            </ListGroup.Item>

            <ListGroup.Item>
                <Row>
                    <Col>
                     Total:
                    </Col>
                    <Col>
                    ${totalPrice}
                    </Col>
                    
                </Row>
            </ListGroup.Item>

           <ListGroup.Item>
            {error &&<Message variant='danger'>{error}</Message>}
           </ListGroup.Item>
            <ListGroup.Item>
                <Button type='button'className='btn-block' disabled={cart.cartItems.length===0} onClick={placeOrder}>Place Order</Button>
                
 
            </ListGroup.Item>
            </ListGroup> 
        </Card>
        </Col>
      </Row>
    </div>
  )
}

export default PlaceOrderScreen
