
import React, {useState, useEffect} from 'react'
import { Link ,useNavigate,useParams } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { Form, Button, Row, Col } from 'react-bootstrap'
import Loader from '../components/Loader'
import Message from '../components/Message'
import FormContainer from '../components/FormContainer'
import {listProductDetails} from '../actions/productActions'
import { productUpdate } from '../actions/productActions'
import { productUpdateReset } from '../store/productUpdate'
import axios from 'axios'
function ProductEditScreen() {
    const match = useParams();
    const productId = match.id
    const [name,setName] = useState('')
    const [price,setPrice] = useState(0)
    const [image,setImage] = useState('')
    const [brand,setBrand] = useState('')
    const [category,setCategory] = useState('')
    const [countInStock,setCountInStock] = useState(0)
    const [description,setDescription] = useState('')
    const [uploading, setUploading] = useState(false)
    const dispatch = useDispatch()
    let navigate = useNavigate()
    
 
 
    const productDetails = useSelector(state => state.productDetail)
    const { error,  loading , product} = productDetails

    const updateProduct = useSelector(state => state.productUpdate)
    const { error:errorUpdate,  loading:loadingUpdate , success:successUpdate} = updateProduct


     
 
    useEffect(()=>{

        if(successUpdate){
            dispatch(productUpdateReset())
            navigate('/admin/productlist') //redirect to product list after success
        }
        else{
            if(! product.name || product._id !== Number(productId)){
                dispatch(listProductDetails(productId))  //fetch product details using productId
              }else{
                
                setName(product.name)
                setPrice(product.price)
                setImage(product.image)
                setBrand(product.brand)
                setCategory(product.category)
                setCountInStock(product.countInStock)
                setDescription(product.description)
        
              }
        }

      
    },[dispatch,product.name, productId ,navigate,successUpdate])
 
 
    const submitHandler = (e) => {
        e.preventDefault()
        dispatch(productUpdate({_id:productId, name, price, image, brand, category, countInStock, description}))  //update product details using productId

    }


    const uploadFileHandler = async (e) => {
        const file = e.target.files[0]  //i.e first file you choose
        const formData = new FormData()
        formData.append('image', file)
        formData.append('product_id', productId)
        setUploading(true)
        try{
            const config = {
                headers: {
                    'Content-Type':'multipart/form-data',
                }
            }
            const {data}=await axios.post('/api/products/upload/',formData,config)
            setImage(data)
             setUploading(false);


        }
        catch{
            setUploading(false)
        }

        
    }
 
  return (
    <div>
        <Link to ='/admin/productlist'>
        Go Back
        </Link>
        <FormContainer>
            <h1>Edit Product</h1>
             {loadingUpdate && <Loader/>}
             {errorUpdate && <Message variant = 'danger'>{errorUpdate}</Message>}
            {loading ? <Loader /> :error ? <Message variant = 'danger'>{error}</Message> :(
                  <Form onSubmit = {submitHandler}>
                  <Form.Group controlId = 'name'>
                              <Form.Label>Name</Form.Label>
                              <Form.Control  type='name' placeholder = 'Enter Name' value={name} onChange={(e) => setName(e.target.value)}></Form.Control>
                  </Form.Group>

                  <Form.Group controlId = 'price'>
                              <Form.Label>Price</Form.Label>
                              <Form.Control  type='number' placeholder = 'Enter Price' value={price} onChange={(e) => setPrice(e.target.value)}></Form.Control>
                  </Form.Group>

                  <Form.Group controlId = 'image'>
                              <Form.Label>image</Form.Label>
                              <Form.Control  type='text' placeholder = 'Enter Image' value={image} onChange={(e) => setImage(e.target.value)}></Form.Control>
                              <Form.Control
                                    type='file'
                                    id='image-file'
                                    onChange={uploadFileHandler}
                                />
                                <div>{uploading && <Loader />}</div>
                  </Form.Group>

                  

                  <Form.Group controlId = 'brand'>
                              <Form.Label>Brand</Form.Label>
                              <Form.Control  type='text' placeholder = 'Enter Brand' value={brand} onChange={(e) => setBrand(e.target.value)}></Form.Control>
                  </Form.Group>

                  <Form.Group controlId = 'description'>
                              <Form.Label>Description</Form.Label>
                              <Form.Control  type='text' placeholder = 'Enter Description' value={description} onChange={(e) => setDescription(e.target.value)}></Form.Control>
                  </Form.Group>

                  <Form.Group controlId = 'category'>
                              <Form.Label>Category</Form.Label>
                              <Form.Control  type='text' placeholder = 'Enter Category' value={category} onChange={(e) => setCategory(e.target.value)}></Form.Control>
                  </Form.Group>

                  <Form.Group controlId = 'count'>
                              <Form.Label>countInStock</Form.Label>
                              <Form.Control  type='number' placeholder = 'Enter Stock' value={countInStock} onChange={(e) => setCountInStock(e.target.value)}></Form.Control>
                  </Form.Group>
                  
                  <Button type='submit' variant = 'primary' className='my-3'>Update</Button>
                  </Form>
            )}
        </FormContainer>
    </div>
  )
}
 
export default ProductEditScreen;

